export default{
	data:function(){
		return {
			phoneClass:'',
			codeClass:''
		}
	},
	mounted(){
		uni.setNavigationBarTitle({
		　　title:'手机登录'
		})
	},
	methods:{
		phoneFocus(e){
			this.phoneClass="focus"
		},
		phoneBlur(e){
			this.phoneClass=""
		},
		codeFocus(e){
			this.codeClass="focus"
		},
		codeBlur(e){
			this.codeClass=""
		},
		login(){
			uni.showToast({
				title:'请输入正确的手机号',
				duration:2000
			})
			// uni.switchTab({
			// 	url:"../index/index"
			// })
		}
	}
}
